let product_Reducer = (state = initialState, action) => {
    switch (action.type) {

        case 'GET_PRODUCT_BY_ID': {
            return {
                ...state,
                currentProduct: action.id

            }

        }
        case 'CLEAR_FINDED_PRODUCTS': {
            return {
                ...state,
                findedProducts: ''

            }
        }
        case 'GET_ANESTHESIA_BY_CHARS': {
            return {
                ...state,
                findedProducts: state.products.filter(product => {
                    if (action.chars.brand == 'all' && product.type == 'anesthesia') {
                        if (action.chars.classes.length) {
                            for (let k = 0; k < action.chars.classes.length; k++) {
                                if (product.clas.includes(action.chars.classes[k])) {
                                    return product;
                                }
                            }
                        } else {
                            return product;
                        }
                    } else if (action.chars.brand != 'all' && product.type == 'anesthesia') {
                        if (action.chars.classes.length) {
                            for (let k = 0; k < action.chars.classes.length; k++) {
                                if (product.clas.includes(action.chars.classes[k])) {
                                    return product.brand == action.chars.brand;
                                }
                            }
                        } else {
                            return product.brand == action.chars.brand;
                        }
                    }
                })
            }
        }
        case 'GET_TOMOGRAPHS_BY_CHARS': {
            return {
                ...state,
                findedProducts: state.products.filter(product => {
                    if (action.chars.brand == 'all' && product.type == 'tomograph') {
                        if (action.chars.imageSizes.length) {
                            for (let i = 0; i < action.chars.imageSizes.length; i++) {
                                if (product.imageSizes.includes(action.chars.imageSizes[i])) {
                                    return product;
                                }
                            }
                        } else {
                            return product;
                        }
                    } else if (action.chars.brand != 'all' && product.type == 'tomograph') {
                        if (action.chars.imageSizes.length) {
                            for (let i = 0; i < action.chars.imageSizes.length; i++) {
                                if (product.imageSizes.includes(action.chars.imageSizes[i])) {
                                    return product.brand == action.chars.brand;
                                }
                            }
                        } else {
                            return product.brand == action.chars.brand;
                        }
                    }
                })
            }
        }
        case 'GET_KTPRODUCTS_BY_CHARS': {
            return {
                ...state,
                findedProducts: state.products.filter(product => {
                    if (action.chars.brand == 'all' && product.type == 'kt') {
                        if (action.chars.slices.length) {
                            for (let i = 0; i < action.chars.slices.length; i++) {
                                if (product.slices.includes(action.chars.slices[i])) {
                                    return product;
                                }
                            }
                        } else {
                            return product;
                        }
                    } else if (action.chars.brand != 'all' && product.type == 'kt') {
                        if (action.chars.slices.length) {
                            for (let i = 0; i < action.chars.slices.length; i++) {
                                if (product.slices.includes(action.chars.slices[i])) {
                                    return product.brand == action.chars.brand;
                                }
                            }
                        } else {
                            return product.brand == action.chars.brand;
                        }
                    }
                })
            }
        }
        case 'GET_MRTPRODUCTS_BY_CHARS': {
            return {
                ...state,
                findedProducts: state.products.filter(product => {

                    if (action.chars.brand == 'all' && product.type == 'mrt') {

                        if (action.chars.pat && action.chars.intensity.length) {
                            for (let i = 0; i < action.chars.intensity.length; i++) {
                                if (product.pat && product.intensity == action.chars.intensity[i]) {
                                    return product;
                                }
                            }
                        } else if (!action.chars.pat && action.chars.intensity.length) {

                            for (let i = 0; i < action.chars.intensity.length; i++) {
                                if (product.intensity == action.chars.intensity[i]) {
                                    return product;
                                }
                            }
                        }
                        else if (action.chars.pat && !action.chars.intensity.length) {
                            return product.pat == true;
                        } else {
                            return product;
                        }
                    }
                    else if (action.chars.brand != 'all' && product.type == 'mrt') {
                        if (action.chars.pat && action.chars.intensity.length) {
                            for (let i = 0; i < action.chars.intensity.length; i++) {
                                if (product.pat && product.intensity == action.chars.intensity[i]) {
                                    return product.brand == action.chars.brand;
                                }
                            }
                        } else if (!action.chars.pat && action.chars.intensity.length) {
                            for (let i = 0; i < action.chars.intensity.length; i++) {
                                if (product.intensity == action.chars.intensity[i]) {
                                    return product.brand == action.chars.brand;
                                }
                            }
                        }
                        else if (action.chars.pat && !action.chars.intensity.length) {
                            if (product.brand == action.chars.brand && product.pat == true) {
                                return product;
                            }

                        } else {
                            return product.brand == action.chars.brand;
                        }
                    }
                })
            }
        }
        case 'GET_UZIPRODUCTS_BY_CHARS': {
            return {
                ...state,
                findedProducts: state.products.filter(product => {
                    if (action.chars.brand == 'all' && product.type == 'uzi') {
                        if (action.chars.classes.length && action.chars.types.length) {
                            for (let i = 0; i < action.chars.types.length; i++) {
                                if (product.typeT.includes(action.chars.types[i])) {
                                    for (let k = 0; k < action.chars.classes.length; k++) {
                                        if (product.clas.includes(action.chars.classes[k])) {
                                            return product;
                                        }
                                    }
                                }
                            }
                        }
                        else if (action.chars.classes.length && !action.chars.types.length) {
                            for (let i = 0; i < action.chars.classes.length; i++) {
                                if (product.clas.includes(action.chars.classes[i])) {
                                    return product;
                                }
                            }
                        } else if (!action.chars.classes.length && action.chars.types.length) {
                            for (let i = 0; i < action.chars.types.length; i++) {
                                if (product.typeT.includes(action.chars.types[i])) {
                                    return product;
                                }
                            }
                        }
                        else {
                            return product;
                        }

                    } else if (action.chars.brand != 'all' && product.type == 'uzi') {
                        if (action.chars.classes.length && action.chars.types.length) {
                            for (let i = 0; i < action.chars.types.length; i++) {
                                if (product.typeT.includes(action.chars.types[i])) {
                                    for (let k = 0; k < action.chars.classes.length; k++) {
                                        if (product.clas.includes(action.chars.classes[k])) {
                                            return product.brand == action.chars.brand;
                                        }
                                    }
                                }
                            }
                        }
                        else if (action.chars.classes.length && !action.chars.types.length) {
                            for (let i = 0; i < action.chars.classes.length; i++) {
                                if (product.clas.includes(action.chars.classes[i])) {
                                    return product.brand == action.chars.brand;
                                }
                            }
                        } else if (!action.chars.classes.length && action.chars.types.length) {
                            for (let i = 0; i < action.chars.types.length; i++) {
                                if (product.typeT.includes(action.chars.types[i])) {
                                    return product.brand == action.chars.brand;
                                }
                            }
                        } else {
                            return product.brand == action.chars.brand;
                        }
                    }
                })
            }
        }
        default: {
            return state;
        }
    }

}

export const getProductById = (id) => {
    return {
        type: 'GET_PRODUCT_BY_ID', id
    }
}
export const getAnesthesiaByChars = (chars) => {
    return {
        type: 'GET_ANESTHESIA_BY_CHARS', chars
    }
}
export const getTomographByChars = (chars) => {
    return {
        type: 'GET_TOMOGRAPHS_BY_CHARS', chars
    }
}
export const getProductByUziChars = (chars) => {
    return {
        type: 'GET_UZIPRODUCTS_BY_CHARS', chars
    }
}
export const getProductByMrtChars = (chars) => {
    return {
        type: 'GET_MRTPRODUCTS_BY_CHARS', chars
    }
}
export const getProductByKtChars = (chars) => {
    return {
        type: 'GET_KTPRODUCTS_BY_CHARS', chars
    }
}
export const clearFindedProducts = () => {
    return {
        type: 'CLEAR_FINDED_PRODUCTS'
    }
}

export default product_Reducer;